.nu_btn{position:relative;display:inline-block;text-align:center;white-space:nowrap;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;vertical-align:middle;-webkit-box-sizing:border-box;box-sizing:border-box;text-decoration:none;-webkit-transition-duration:200ms;-o-transition-duration:200ms;transition-duration:200ms;font:inherit;border:none 0;padding:.5em 1em;background-color:transparent;line-height:1.5em;border-radius:0}.nu_btn._fill{background-color:#333;color:#fff}.nu_btn._ghost{-webkit-box-shadow:inset 0 0 0 1px currentColor;box-shadow:inset 0 0 0 1px currentColor}.nu_btn[disabled],.nu_btn:disabled,.nu_btn._loading{cursor:not-allowed;pointer-events:none}.nu_btn._capsule{border-radius:100px}.nu_btn._block{display:block;width:100%}.nu_btn._circle{padding:0;border-radius:100px}