.btn_bootstrap._primary:not(._fill) {
  color: #007bff;
}
.btn_bootstrap._primary._fill {
  background-color: #007bff;
}
.btn_bootstrap._secondary:not(._fill) {
  color: #6c757d;
}
.btn_bootstrap._secondary._fill {
  background-color: #6c757d;
}
.btn_bootstrap._warning:not(._fill) {
  color: #ffc107;
}
.btn_bootstrap._warning._fill {
  background-color: #ffc107;
}
.btn_bootstrap._success:not(._fill) {
  color: #28a745;
}
.btn_bootstrap._success._fill {
  background-color: #28a745;
}
.btn_bootstrap._danger:not(._fill) {
  color: #dc3545;
}
.btn_bootstrap._danger._fill {
  background-color: #dc3545;
}
.btn_bootstrap {
  font-size: 1rem;
  border-radius: 0.25rem;
  text-transform: capitalize;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.btn_bootstrap._large {
  height: 48px;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
}
.btn_bootstrap._small {
  height: 32px;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  border-radius: 0.2rem;
}
